"use client";

import { useState, useEffect } from "react";
import Navbar from "../../components/Navbar";
import { useRouter } from "next/navigation";
import Swal from "sweetalert2";
import { getRelativeTime } from "../../lib/utils";

interface Ticket {
  globalId: string | number;
  id: number;
  title: string;
  description: string;
  category?: string;
  status: string;
  createdBy: string;
  dept: string;
  date: string;
  userMarkedDone?: boolean;
  headMarkedDone?: boolean;
  lastUpdated?: string;
}

export default function TicketsPage() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);
  const [tickets, setTickets] = useState<Ticket[]>([]);
  const [selectedTicket, setSelectedTicket] = useState<Ticket | null>(null);
  const [activeTab, setActiveTab] = useState("All");
  const [categoryFilter, setCategoryFilter] = useState("All");
  const [isLoading, setIsLoading] = useState(true);
  const [sortConfig, setSortConfig] = useState<{
    key: keyof Ticket;
    direction: "asc" | "desc";
  } | null>({ key: "id", direction: "asc" });

  // 1. Load User and Fetch Data
  useEffect(() => {
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      const parsedUser = JSON.parse(storedUser);
      setUser(parsedUser);
      fetchTickets(parsedUser);
    } else {
      router.push("/login");
    }
  }, [router]);

  const fetchTickets = async (currentUser: any) => {
    setIsLoading(true);

    try {
      const res = await fetch("http://localhost:3001/api/tickets/");

      if (res.ok) {
        const serverTickets = await res.json();

        const localData = localStorage.getItem("myTickets");

        if (localData) {
          const localTickets = JSON.parse(localData);

          const merged = serverTickets.map((sTicket: Ticket) => {
            const localMatch = localTickets.find(
              (l: Ticket) => l.globalId === sTicket.globalId,
            );

            return localMatch ? { ...sTicket, ...localMatch } : sTicket;
          });

          setTickets(merged);
        } else {
          setTickets(serverTickets);
        }
      }
    } catch (error) {
      console.error("Error fetching tickets:", error);
    } finally {
      setIsLoading(false);
    }
  };

  // 2. Status Change Logic

  const handleStatusChange = (globalId: string | number, newStatus: string) => {
    const updatedTickets = tickets.map((ticket) => {
      if (ticket.globalId === globalId) {
        const isHead = user?.role === "Head";

        const isUser = user?.role === "User";

        if (newStatus === "In Progress") {
          return {
            ...ticket,

            status: "In Progress",

            lastUpdated: new Date().toISOString(),
          };
        }

        const updatedTicket = {
          ...ticket,

          headMarkedDone: isHead ? true : ticket.headMarkedDone || false,

          userMarkedDone: isUser ? true : ticket.userMarkedDone || false,

          lastUpdated: new Date().toISOString(),
        };

        if (updatedTicket.headMarkedDone && updatedTicket.userMarkedDone) {
          updatedTicket.status = "Resolved";
        }

        return updatedTicket;
      }

      return ticket;
    });

    setTickets(updatedTickets);

    localStorage.setItem("myTickets", JSON.stringify(updatedTickets));

    Swal.fire({
      toast: true,

      position: "top-end",

      icon: "success",

      title: "Status Updated",

      showConfirmButton: false,

      timer: 1500,
    });
  };

  const handleSort = (key: keyof Ticket) => {
    let direction: "asc" | "desc" = "asc";

    if (sortConfig?.key === key && sortConfig.direction === "asc") {
      direction = "desc";
    }

    setSortConfig({ key, direction });
  };

  // 3. Filter & Sort Logic

  const filteredByRole = tickets.filter((ticket) => {
    if (!user) return false;

    if (user.role === "Head") return ticket.dept === user.dept;

    return ticket.createdBy === user.username;
  });

  const filteredTickets = filteredByRole.filter((ticket) => {
    const statusMatch = activeTab === "All" || ticket.status === activeTab;

    const categoryMatch =
      categoryFilter === "All" || ticket.category === categoryFilter;

    return statusMatch && categoryMatch;
  });

  const sortedTickets = [...filteredTickets].sort((a, b) => {
    if (!sortConfig) return 0;

    const { key, direction } = sortConfig;

    const valA = a[key as keyof Ticket] ?? "";

    const valB = b[key as keyof Ticket] ?? "";

    if (valA < valB) return direction === "asc" ? -1 : 1;

    if (valA > valB) return direction === "asc" ? 1 : -1;

    return 0;
  });

  if (!user || isLoading) {
    return (
      <div className="flex justify-center items-center h-screen bg-gray-50 text-gray-500">
        Loading Tickets...
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 relative">
      <Navbar user={user} />

      <main className="max-w-7xl mx-auto py-10 px-8">
        <div className="bg-white overflow-hidden shadow-sm sm:rounded-lg border border-gray-200">
          <div className="p-8">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-2xl font-bold text-gray-900">
                Ticket Management
              </h2>

              {user.role === "User" && (
                <button
                  onClick={() => router.push("/tickets/create")}
                  className="bg-green-600 text-white font-medium py-2 px-6 rounded-lg hover:bg-green-700 transition shadow-md flex items-center gap-2"
                >
                  <span className="text-lg">+</span> Create New Ticket
                </button>
              )}
            </div>

            {/* TAB NAVIGATION */}

            <div className="flex gap-8 border-b border-gray-100 mb-6">
              {["All", "Pending", "In Progress", "Resolved"].map((tab) => (
                <button
                  key={tab}
                  onClick={() => setActiveTab(tab)}
                  className={`pb-4 text-sm font-medium transition-all relative flex items-center gap-2 ${
                    activeTab === tab
                      ? "text-blue-600"
                      : "text-gray-400 hover:text-gray-600"
                  }`}
                >
                  {tab}

                  <span
                    className={`text-[10px] px-2 py-0.5 rounded-full ${activeTab === tab ? "bg-blue-100 text-blue-600" : "bg-gray-100 text-gray-500"}`}
                  >
                    {tab === "All"
                      ? filteredByRole.length
                      : filteredByRole.filter((t) => t.status === tab).length}
                  </span>

                  {activeTab === tab && (
                    <div className="absolute bottom-0 left-0 right-0 h-0.5 bg-blue-600 rounded-full" />
                  )}
                </button>
              ))}
            </div>

            {/* TABLE */}

            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    {[
                      { label: "ID", key: "id" },

                      { label: "Sender", key: "createdBy" },

                      { label: "Category", key: "category" },

                      { label: "Title", key: "title" },

                      { label: "Status", key: "status" },

                      { label: "Date", key: "date" },

                      { label: "Last Updated", key: "lastUpdated" },
                    ].map((header) => (
                      <th
                        key={header.key}
                        onClick={() => handleSort(header.key as keyof Ticket)}
                        className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100"
                      >
                        <div className="flex items-center gap-1">
                          {header.label}{" "}
                          {sortConfig?.key === header.key &&
                            (sortConfig.direction === "asc" ? "ðŸ”¼" : "ðŸ”½")}
                        </div>
                      </th>
                    ))}

                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>

                <tbody className="bg-white divide-y divide-gray-200">
                  {sortedTickets.length > 0 ? (
                    sortedTickets.map((ticket, index) => (
                      <tr
                        key={ticket.globalId}
                        className="hover:bg-gray-50/50 transition"
                      >
                        <td className="px-6 py-4 text-sm text-gray-900">
                          #{ticket.id}
                        </td>

                        <td className="px-6 py-4 text-sm font-medium text-gray-600">
                          {ticket.createdBy}
                        </td>

                        <td className="px-6 py-4 text-sm text-gray-500">
                          {ticket.category || "General"}
                        </td>

                        <td className="px-6 py-4 text-sm text-gray-900">
                          {ticket.title}
                        </td>

                        <td className="px-6 py-4">
                          <span
                            className={`px-3 py-1 text-xs font-bold rounded-full ${
                              ticket.status === "Pending"
                                ? "bg-yellow-100 text-yellow-800"
                                : ticket.status === "In Progress"
                                  ? "bg-blue-100 text-blue-800"
                                  : "bg-green-100 text-green-800"
                            }`}
                          >
                            {ticket.status}
                          </span>
                        </td>

                        <td className="px-6 py-4 text-sm text-gray-500">
                          {new Date(ticket.date).toLocaleDateString()}
                        </td>

                        <td className="px-6 py-4 text-sm text-gray-500">
                          {ticket.status === "Resolved"
                            ? `Resolved on ${new Date(ticket.lastUpdated || ticket.date).toLocaleDateString()}`
                            : getRelativeTime(ticket.lastUpdated)}
                        </td>

                        <td className="px-6 py-4 text-sm font-medium">
                          <div className="flex items-center space-x-2">
                            {ticket.status !== "Resolved" && (
                              <button
                                onClick={() => setSelectedTicket(ticket)} // Triggers a modal
                                className="px-3 py-1 bg-indigo-600 text-white rounded text-sm font-medium"
                              >
                                View Details
                              </button>
                            )}

                            {ticket.status === "Pending" &&
                              user.role === "Head" && (
                                <button
                                  onClick={() =>
                                    handleStatusChange(
                                      ticket.globalId,

                                      "In Progress",
                                    )
                                  }
                                  className="px-3 py-1 bg-green-600 text-white rounded text-xs font-bold"
                                >
                                  Accept
                                </button>
                              )}

                            {ticket.status === "In Progress" && (
                              <>
                                {(user.role === "Head" &&
                                  !ticket.headMarkedDone) ||
                                (user.role === "User" &&
                                  !ticket.userMarkedDone) ? (
                                  <button
                                    onClick={() =>
                                      handleStatusChange(
                                        ticket.globalId,

                                        "Resolved",
                                      )
                                    }
                                    className="px-3 py-1 bg-blue-600 text-white rounded text-xs font-bold"
                                  >
                                    Mark Done
                                  </button>
                                ) : (
                                  <span className="text-gray-400 italic text-xs">
                                    Waiting...
                                  </span>
                                )}
                              </>
                            )}

                            {ticket.status === "Resolved" && (
                              <span className="text-green-600 text-xs font-bold">
                                âœ“ Resolved
                              </span>
                            )}
                          </div>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td
                        colSpan={8}
                        className="px-6 py-10 text-center text-gray-500"
                      >
                        No tickets found.
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
